.about {
  margin-top: 150px;
}
.about-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  margin-top: 50px;
  gap: 50px;
}

.about-description {
  width: 40%;
  height: 100%;
  padding: 20px;
  color: white;
  font-weight: 400;
  font-size: 20px;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.5rem;
}

.about-image {
  width: 30%;
}
